var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page"},[_c('div',{staticClass:"brief"},[_c('span',[_vm._v("55S3IDE5OTMuMDEuMTYgbGVpeWFuZ19hY2VAMTYzLmNvbQ==")])]),_c('div',{staticClass:"article"},[_c('h3',[_vm._v("上学阶段")]),_c('p',[_vm._v(" 接触计算机应该是上初中那会儿（05年左右），当时学校有一个微机室，里面也就30-40台电脑，而一个班的学生过百，平均下来3人一台。电脑是老式的“大屁股”， 鼠标是下方带个球的机械鼠标，常常需要抠下来在衣服上蹭蹭才能用。由于人数太多，老师也顾不到所有人，就安排几个对计算机比较熟悉的学生 同时指导我们这些一无所知的学生。当时电子产品不像现在这般普及，一般家庭里也就电视机 、收音机，稍好一些会有磁带机、DVD机或是学习机（小霸王游戏机）。拥有电脑的 就更稀少了，那可是很金贵的物件，所以在上微机课时大家一般都小心翼翼，操作每一步都很小心，跟拆炸弹似的，现在想来还有些滑稽。由于资源紧张，全校只有这一个微机室， 每学期能上2-3次就很不错了，那是当时唯三感觉45分钟很短的，其他两个是体育课和音乐课。 ")]),_c('p',[_vm._v(" 上了高中后硬件更好一些，但是微机课仍然得不到保证，常常变成自习课。一晃就到了高考，然后填报志愿，这是我这辈子最后悔的事情，多想时光倒流啊， 回到那时，将第一志愿改了，或许大学四年会过的更好吧。 ")]),_c('p',[_vm._v(" 进入大学后，学校开设了计算机基础公选课，图书馆里的电脑数量也足够，逐渐接触了Word、PPT、PS、Flash等软件，虽然看不太懂，但是学的不亦乐乎。 后来从哥哥那里得到了人生中第一台电脑，如获至宝。电脑很旧，配置很低，用一段时间后网卡坏了，南桥也坏了，大修了一次，后来干脆将主板和CPU换了， 总共花了不到1000块，还是托的舍友担保进行分期付款。期间，有朋友给介绍了PHP，就从图书馆借了几本相关的书学习了一下，凭着公选课学到的那点儿 C 基础 看的糊里糊涂。就这样到了大四，眼看着别人都签了三方协议，就业的压迫感越来越强。几番折腾，总算在2016年08月01日正式”出道“，成为了一名低级社畜。 ")]),_c('h5',[_vm._v("总结")]),_c('p',[_vm._v(" 回看整个上学阶段，在大学之前一直都是一个本分、守纪律的学生，上大学后开始夜机、旷课、作弊，变成了一个十足的”坏“学生。 到了大学后期，情况变得更糟，自己对自己很不满，但又不知道怎么改变，每天都感觉很愤怒，便把精力全部发泄在篮球和游戏上，每天除了吃饭睡觉就是打，白天打球，晚上打游戏。 唉，一开始，大学生涯就歪了，而过程又没有把握住，错上加错，整个上学阶段因为糟糕结尾而失败。况且，这一切都是自己选的，怨不得别人。 ")]),_c('h3',[_vm._v("工作阶段")]),_c('p',[_vm._v(" 由于失败的大学经历导致和别人拉开的距离，立志要在工作中赶上来。 ")]),_c('p',[_vm._v(" 参加工作的第一年，由于自己一无所长，在朋友的介绍下进入一家新材料公司，工作很脏很累，每天8小时，下午很早就下班了，自己有很多可支配时间， 便鼓捣起 Arduino，买了一套开发套件和一堆传感器，玩一段时间后发现要玩的好还是要自己写程序，于是又买了本 C++ Primer 开始学习 C++ ，底气还是来自于大学公选课时学的 C 😂。 后来，被公司外派到佛山驻厂学习，在此期间有朋友向我推荐 Python，我半信半疑地搜索了一下 Python，手机里下载一个叫 狂飙Python 的APP，里面只是 Python 的基础语法介绍， 相比于复杂枯燥的C++，突然出现这么一个语法简单，又能很快上手的语言，真是喜出望外，当时上厕所时都在学习。那是2017年5月下旬到6月上旬，正在播NBA总决赛。 就这样，在那段时间，入坑 Python。 ")]),_c('p',[_vm._v(" 后来过了两个月，我辞职了，开始全身心投入自学 Python，当时有考虑过报培训班，最后还是没报，一是因为没钱，二是怕破坏学习的乐趣。 当时入门的是爬虫，感觉很是神奇，随着了解的越来越多，又对 web开发 产生了兴趣 ... ")]),_c('p',[_vm._v(" 这一晃又四年（2020年07月02日）过去了，四年间自己一直在学习，用到的，用不到的，没有界限，不限方向，不断地尝试， 慢慢懂得自己需要什么，也渐渐找到自己努力的方向。 ")]),_c('p',[_vm._v(" 下个四年，我要变的更强！ ")])])])}]

